.game-page {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-top: 20px;
}

.game {
  display: flex;
  flex-direction: column;
}

@media only screen and (max-width: 768px) {
  .game-page {
    flex-direction: column;
    align-items: center;
    padding-top: 10px;
  }
}